import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Container, Accordion, Card, Button} from "react-bootstrap";

function Details() {
return (
    <div id="details" className="text-light bg-dark">
        <Container className="pt-5 pb-5">
            <h2 className="pt-4 pb-4 text-center">Подробнее</h2>
            <Accordion className="text-center">
                <Card className="bg-dark">
                    <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="0">
                            На чём написан сайт?
                        </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="0">
                        <Card.Body>
                            <p>Я обычно пишу с ReactJS (очень уж она мне понравился), также я использую Bootstrap и Fontawesome, чтобы быстро верстать.<br/>
                            <span className="small">Дизайнер из меня может и не хороший, но если сильно надо, то могу и подумать, надеюсь, сайт в этом плане нормально выглядит.</span></p>
                        </Card.Body>
                    </Accordion.Collapse>
                </Card>
                <Card className="bg-dark">
                    <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="1">
                            В чём смысл сайта?
                        </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="1">
                        <Card.Body>
                            <p>Ни в чём. Отчасти я хотел создать проект, который можно было бы добавить себе в портфолио, но получилось вот так. <br/>
                            Я долго развивался в этом направлении и два года просто учился, прогресс шёл медленно, но я всё ждал помента, когда созрею<br/>
                            для своих шаблонах и сделаю личную страницу и перестану уже баловаться в песочнице. По-моему, для начала достаточно.
                            </p>
                        </Card.Body>
                    </Accordion.Collapse>
                </Card>
                <Card className="bg-dark">
                    <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="2">
                            Можно ли заказать подобный сайт?
                        </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="2">
                        <Card.Body><p>
                            Возможно, для этого стоит обратиться ко мне по моим контактам. Отвечу везде, но ВК предпочтительнее.<br/>
                            Врать не стану, на заказ я только для друзей делал сайты, где ТЗ было необязательным и создавалось на ходу, <br/>
                            но если Вам всё-таки нужна помощь, то я попробую, но не обещаю, что всё будет идеально. <br/>
                            Я специализируюсь скорее на верстке и front-end, с back-end я тоже знаком, но на очень начальном уровне.
                        </p></Card.Body>
                    </Accordion.Collapse>
                </Card>
            </Accordion>
        </Container>
    </div>
)
}
export default Details;